.fxch-benefits {
    &-info {
        &-title {
            color: var(--f-text);
        }
        &-list-item {
            &:nth-of-type(2) {
                .fxch-benefits-info-list-item-marker {
                    &:after {
                        background-color: var(--d-color-first-600);
                    }
                }
            }
            &:nth-of-type(3) {
                .fxch-benefits-info-list-item-marker {
                    &:after {
                        background-color: var(--d-color-second-600);
                    }
                }
            }
            &-marker {
                background-color: var(--b-card);
                box-shadow: var(--box-shadow);
                &:after {
                    background-color: var(--accent-500);
                }
            }
            &-text {
                color: var(--f-text);
            }
        }
    }
    &-decoration {
        border-color: var(--f-divider);
        background-color: var(--b-card);
        backdrop-filter: blur(40px);
    }
}