.fxch-contact {
    &-form-wrap {
        background-color: var(--b-bg);
    }
    &-info {
        &-wrap {
            background-color: var(--accent-500);
        }
        &-title,
        &-text,
        &-link {
            color: var(--accent-500-contrast);
        }
        &-link {
            &:hover {
                color: var(--accent-500-contrast);
            }
        }
    }
}