.fxch-footer {
    &-content {
        background-color: var(--f-text);
        color: var(--b-card);
    }
    &-logo-img {
        .fxch-logo {
            --main-logo: var(--b-card);
        }
    }
    &-bottom {
        color: var(--b-card-secondary);
    }
    &-contacts {
        &-link {
            color: var(--b-card);
            &:hover {
                color: var(--b-card);
                .fxch-footer-contacts-link-icon {
                    color: var(--b-card-secondary);    
                }
            }
            &-icon {
                color: var(--b-card-secondary);
            }
        }
        &-copy {
            background-color: var(--b-footer-link-bg);
            color: var(--b-card);
            &:hover {
                background-color: var(--b-footer-link-bg-hover);
            }
        }
        &-label {
            color: var(--b-card);
            background-color: var(--b-toaster-bg);
            border-color: var(--f-divider);
        }
    }
    &-additional-item {
        color: var(--b-card);
        border-color: var(--b-card);
        background-color: rgba(0,0,0,0);
        &:hover {
            color: var(--f-text);
            background-color: var(--b-card);
        }
    }
}